.product-price-tag {
  color: #bdbdbd;
  font-size: 16px;
  margin-left: 15px;
}
.product-quantity div {
  margin-right: 10px;
}
.counter-btn {
  color: #6f9642;
  border: none;
  border-radius: 50%;
}
.minus-btn {
  padding: 0px 8px;
}
.plus-btn {
  padding: 0px 7px;
}
.product-quantity {
  font-size: 16px;
}
.product-variant div {
  margin-right: 10px;
}
.varient-color-btn {
  border: none;
  border-radius: 50%;
  padding: 0px;
  background-color: white;
}
.variant-input {
  border: 1px solid transparent !important;
  border-bottom: 1px solid #bdbdbd !important;
  height: 30px;
}

.variant-input:active {
  border: 1px solid transparent !important;
  border-color: white !important;
}
.cart-caret {
  padding: 0px 2px !important;
  background-color: white !important;
}
.cart-list {
  top: 20px !important;
  left: 15px !important;
  width: 18rem !important;
}

.cart-item-detail p,
h6 {
  margin-bottom: 0px !important;
}
.cart-list-item {
  display: block;
  position: relative;
  width: 100%;
  padding: 0.25rem 1rem;
  clear: both;
  font-weight: 400;
  color: #212529;
  text-align: inherit;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border: 0;
}
.remove-cart-item {
  position: absolute;
  right: 12px;
  top: 2px;
  cursor: pointer;
}
.active-variant-value {
  border: 2px solid #6f9642;
}
.valueVariantImage {
  cursor: pointer;
}
.paginate-prev,
.paginate-next,
.paginate-item {
  border-radius: 50% !important;
}
.paginate-prev i,
.paginate-next i {
  color: #707070;
}
.paginate-item {
  color: #6f9642 !important;
  margin-left: 10px !important;
  margin-right: 10px !important;
}
.page-item .active {
  color: #fff !important;
  background-color: #6f9642;
}
.order-summary-box {
  border: 1px solid #c7c7c7;
}
.summary-detail {
  padding: 15px 15px 5px 15px;
}
.summary-detail div {
  font-size: 15px;
}
.summary-attrib {
  color: #787878;
}
.order-summary-box .section-1 {
  margin-bottom: 25px;
}
.cartItem-remove {
  position: absolute;
  top: 2px;
  right: 2px;
}
.cartItem-remove a i {
  border-radius: 50%;
  background-color: #fb5402;
  color: white;
  padding: 4px 5px !important;
}
.secondary-border {
  border-bottom: 1px solid #c7c7c7;
}
.summary-detail-total {
  padding: 5px 15px 5px 15px;
}
.summary-detail-total .summary-attrib {
  font-size: 20px;
  color: #000000;
}
.summary-detail-total .summary-attrib-val {
  font-size: 20px;
  color: #6f9642;
}
.user_rating_star span {
  margin: 0px 5px;
}
.order-detail-tab {
  cursor: pointer;
  font-weight: 600;
}
.order-detail-tab .nav-link {
  color: #495057;
}
.order-detail-tab .nav-link:hover {
  color: #495057;
}
.order-detail-tab .nav-link.active {
  border-color: #fff #fff #6f9642 !important;
  border-bottom: 3px solid;
}
.text-orange-light {
  color: #fb5402;
}
.swiper-button-prev,
.swiper-button-next {
  color: #6f9642 !important;
}
.order_details_box {
  border-radius: 25%;
  border: 1px solid #c7c7c7;
}
.step-line {
  background-color: #c7c7c7;
}
.steps-wizard {
  position: relative;
  background-color: #c7c7c7;
  height: 1.5px;
  margin: 0px 4vw;
}
.step-circle {
  border: 1px solid #c7c7c7;
  color: white;
  background-color: #6f9642;
  height: 40px;
  width: 40px;
  position: relative;
  border-radius: 50%;
  margin: auto;
}
.step-circle .step-circle-count {
  position: absolute;
  top: 20%;
  left: 40%;
  font-weight: 600;
  font-size: 14px;
}
#chat .you .triangle {
  border-color: transparent transparent #f6f6f6 transparent;
  margin-left: 15px;
}
#chat .triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 10px 10px;
}
#chat .you .message {
  background-color: #f6f6f6;
}
#chat .message {
  padding: 10px;
  color: black;
  line-height: 25px;
  display: inline-block;
  width: 100%;
  text-align: left;
  border-radius: 5px;
}
.table-responsive::-webkit-scrollbar-thumb:hover {
  background: #c1c1c1;
  transition: 0.5s !important;
}
.table-responsive::-webkit-scrollbar {
  width: 5px;
  height: 7px;
  transition: 0.5s !important;
}
/* Track */
.table-responsive::-webkit-scrollbar-track {
  background: #fff;
}
/* Handle */
.table-responsive::-webkit-scrollbar-thumb {
  background: #6f9642;
  border-radius: 5px;
  transition: 0.5s !important;
}
.search-container {
  height: 100vh;
  margin-top: 10px;
  overflow-y: auto;
  overflow-x: hidden;
  margin-bottom: 15px;
}
